@import '../../assets/variables';

footer {
    background: $color-primary;
    padding: 3rem 0 8rem;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;

    a {
        color: $color-bg;
    }
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;

    > a {
        background: $color-bg;
        color: $color-primary;
        padding: 0.8rem;
        border-radius: 0.7rem;
        display: flex;
        border: 1px solid transparent;

        &:hover {
            background: transparent;
            color: $color-bg;
            border-color: $color-bg;
        }
    }
}

.footer__copyright {
    > small {
        margin-bottom: 4rem;
        color: $color-bg;
    }
}

@include media(sm) {
    .permalinks {
        flex-direction: column;
        gap: 1.5rem;
    }

    .footer__socials {
        margin-bottom: 2.6rem;
    }
}